import {connect} from 'react-redux';

import {getChannelMembersInChannels} from 'mattermost-redux/selectors/entities/channels';
import {getPost} from 'mattermost-redux/selectors/entities/posts';
import {getUserByUsername} from 'mattermost-redux/selectors/entities/users';

import PostMessageAttachment from './post_message_attachment.jsx';

const mapStateToProps = (state, ownProps) => {
    const members = getChannelMembersInChannels(state);
    if (!members) {
        return null;
    }

    const post = getPost(state, ownProps.postId);
    if (!post) {
        return null;
    }

    const pattern = /@\S+ /g;
    const usernames = post.message.match(pattern);
    if (!usernames) {
        return null;
    }
    
    let users =[];
    usernames.forEach(username => {
        username = username.replace(/^@/, "").replace(/ $/, "");
        const user = getUserByUsername(state, username);
        const channel_user = members[post.channel_id][post.user_id];
        if (channel_user) {
            if (channel_user.last_viewed_at < post.create_at) {
                users.push(user);
            }
        }
    });

    return {
        users,
    };
};

export default connect(mapStateToProps)(PostMessageAttachment);
